import React from 'react';
import { useForm } from "react-hook-form";
import { auth } from '../../firebase';
import { Flex } from '../Lib';
import { LoginBackground, LoginContainer, LoginInput, LoginMainButton } from './styles';

function Login() {
  const { register, handleSubmit } = useForm();

  // Typescript bug, la definizione corretta sarebbe { email, password }: { email: string, password: string }
  const onSubmit = ({ email, password }: any) => {
    auth.signInWithEmailAndPassword(email, password)
    .catch(error => console.log('Error Signing in with email and password'));
  }
  return (<>
    <LoginBackground />
    <LoginContainer>
      <Flex flexDirection="column">
        <h1>BiBooking</h1>
      </Flex>
      <form onSubmit={handleSubmit(onSubmit)}>
        <LoginInput name="email" ref={register({ required: true })} placeholder="Email" />
        <LoginInput name="password" ref={register({ required: true })} placeholder="Password" />
        <LoginMainButton type="submit">Log in</LoginMainButton>
      </form>
    </LoginContainer>
  </>);
}

export default Login;
